@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");
/*BOXED LAYOUT*/
/*GRID - media queries breakpoints*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  color: #444;
  line-height: 1.9;
  background-color: #f3f3f3;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.container-fluid {
  height: 100%;
  width: 100%;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .container-fluid {
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1024px;
  }
}
.overflow-hidden {
  overflow: hidden;
}

:root {
  --color-primary: #5ec576;
  --color-secondary: #ffcb03;
  --color-tertiary: #ff585f;
  --color-primary-darker: #4bbb7d;
  --color-secondary-darker: #ffbb00;
  --color-tertiary-darker: #fd424b;
  --color-primary-opacity: #5ec5763a;
  --color-secondary-opacity: #ffcd0331;
  --color-tertiary-opacity: #ff58602d;
  --gradient-primary: linear-gradient(to top left, #39b385, #9be15d);
  --gradient-secondary: linear-gradient(to top left, #ffb003, #ffcb03);
}

h1 {
  font-size: 5.5rem;
  line-height: 1.35;
}

h4 {
  font-size: 2.4rem;
  font-weight: 500;
}

p {
  color: #666;
}

button:focus {
  outline: none;
}

img {
  transition: filter 0.5s;
}

.btn {
  display: inline-block;
  background-color: var(--color-primary);
  font-size: 1.6rem;
  font-family: inherit;
  font-weight: 500;
  border: none;
  padding: 1.25rem 4.5rem;
  border-radius: 10rem;
  cursor: pointer;
  transition: all 0.3s;
  color: #444;
}

.btn:hover {
  background-color: var(--color-primary-darker);
}

.btn--text {
  display: inline-block;
  background: none;
  font-size: 1.7rem;
  font-family: inherit;
  font-weight: 500;
  color: var(--color-primary);
  border: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  cursor: pointer;
  transition: all 0.3s;
}

.lazy-img {
  filter: blur(20px);
}

@media (max-width: 575px) {
  .btn {
    font-size: 1.4rem;
    padding: 1rem 2rem;
  }
}
/* GENERAL ELEMENTS */
.section {
  padding-top: 15rem;
  padding-bottom: 15rem;
  border-top: 1px solid #ddd;
  transition: transform 1s, opacity 1s;
}
.section--hidden {
  opacity: 0;
  transform: translateY(8rem);
}
.section__title {
  max-width: 80rem;
  margin: 0 auto 8rem auto;
}
.section__title--testimonials {
  margin-bottom: 4rem;
}
.section__description {
  font-size: 1.8rem;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--color-primary);
  margin-bottom: 1rem;
}
.section__header {
  font-size: 4rem;
  line-height: 1.3;
  font-weight: 500;
}

/* FEATURES */
.features__header {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.features__img {
  width: 45%;
}
.features__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15rem;
}
.features__item:last-child {
  margin-bottom: 0;
}
.features__item--reverse {
  flex-direction: row-reverse;
}
.features__feature {
  align-self: center;
  justify-self: center;
  width: 45%;
  font-size: 1.5rem;
}
.features__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-primary-opacity);
  height: 5.5rem;
  width: 5.5rem;
  border-radius: 50%;
  margin-bottom: 2rem;
}
.features__icon svg {
  height: 2.5rem;
  width: 2.5rem;
  fill: var(--color-primary);
}

/* OPERATIONS */
.operations {
  max-width: 100rem;
  margin: 12rem auto 0 auto;
  background-color: #fff;
}
.operations .operations__tab-container {
  display: flex;
  justify-content: center;
}
.operations__tab {
  margin-right: 2.5rem;
  transform: translateY(-50%);
}
.operations__tab span {
  margin-right: 1rem;
  font-weight: 600;
  display: inline-block;
}
.operations__tab--1 {
  background-color: var(--color-secondary);
}
.operations__tab--1:hover {
  background-color: var(--color-secondary-darker);
}
.operations__tab--3 {
  background-color: var(--color-tertiary);
  margin: 0;
}
.operations__tab--3:hover {
  background-color: var(--color-tertiary-darker);
}
.operations__tab--active {
  transform: translateY(-66%);
}
.operations__content {
  display: none;
  font-size: 1.7rem;
  padding: 2.5rem 7rem 6.5rem 7rem;
}
.operations__content--active {
  display: grid;
  grid-template-columns: 7rem 1fr;
  column-gap: 3rem;
  row-gap: 0.5rem;
}
.operations__content p {
  grid-column: 2;
}
.operations__header {
  font-size: 2.25rem;
  font-weight: 500;
  align-self: center;
}
.operations__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
}
.operations__icon svg {
  height: 2.75rem;
  width: 2.75rem;
}
.operations__icon--1 {
  background-color: var(--color-secondary-opacity);
}
.operations__icon--1 svg {
  fill: var(--color-secondary-darker);
}
.operations__icon--2 {
  background-color: var(--color-primary-opacity);
}
.operations__icon--2 svg {
  fill: var(--color-primary);
}
.operations__icon--3 {
  background-color: var(--color-tertiary-opacity);
}
.operations__icon--3 svg {
  fill: var(--color-tertiary);
}

/* SLIDER */
.slider {
  max-width: 100rem;
  height: 50rem;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.slider__btn {
  position: absolute;
  top: 50%;
  z-index: 10;
  border: none;
  background: rgba(255, 255, 255, 0.7);
  color: #333;
  border-radius: 50%;
  height: 5.5rem;
  width: 5.5rem;
  font-size: 3.25rem;
  cursor: pointer;
}
.slider__btn--left {
  left: 6%;
  transform: translate(-50%, -50%);
}
.slider__btn--right {
  right: 6%;
  transform: translate(50%, -50%);
}

.slide {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 1s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.dots {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}
.dots__dot {
  border: none;
  background-color: #b9b9b9;
  opacity: 0.7;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  margin-right: 1.75rem;
  cursor: pointer;
  transition: all 0.5s;
}
.dots__dot:last-child {
  margin: 0;
}
.dots__dot--active {
  background-color: #888;
  opacity: 1;
}

/* TESTIMONIALS */
.testimonial {
  width: 65%;
  position: relative;
}
.testimonial::before {
  content: "“";
  position: absolute;
  top: -5.7rem;
  left: -6.8rem;
  line-height: 1;
  font-size: 20rem;
  font-family: inherit;
  color: var(--color-primary);
  z-index: -1;
}
.testimonial__header {
  font-size: 2.25rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
}
.testimonial__text {
  font-size: 1.7rem;
  margin-bottom: 3.5rem;
  color: #666;
}
.testimonial__author {
  margin-left: 3rem;
  font-style: normal;
  display: grid;
  grid-template-columns: 6.5rem 1fr;
  column-gap: 2rem;
}
.testimonial__photo {
  grid-row: 1/span 2;
  width: 6.5rem;
  border-radius: 50%;
}
.testimonial__name {
  font-size: 1.7rem;
  font-weight: 500;
  align-self: end;
  margin: 0;
}
.testimonial__location {
  font-size: 1.5rem;
}

/* SIGNUP */
.section--sign-up {
  background-color: #37383d;
  border-top: none;
  border-bottom: 1px solid #444;
  text-align: center;
}
.section--sign-up .section__header {
  color: #fff;
  text-align: center;
}
.section--sign-up .section__title {
  margin-bottom: 6rem;
}
.section--sign-up .btn {
  font-size: 1.9rem;
  padding: 2rem 5rem;
}

/* COOKIE MESSAGE */
.cookie-message {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  background-color: white;
  color: #bbb;
  font-size: 1.5rem;
  font-weight: 400;
}

@media (max-width: 767px) {
  .features__item {
    flex-direction: column;
    margin-bottom: 5rem;
  }
  .features__img {
    margin-bottom: 4rem;
    width: 100%;
  }
  .features__feature {
    width: 100%;
  }

  .operations__header {
    grid-column: 1/3;
  }
  .operations__content {
    padding: 2rem 3rem;
  }
  .operations__content p {
    grid-column: 1/3;
  }
  .operations__content--active {
    display: grid;
    grid-template-columns: auto;
    column-gap: 3rem;
    row-gap: 0.5rem;
  }

  .testimonial {
    width: 100%;
  }

  .slider__btn {
    top: 70%;
  }
  .slider__btn--right {
    left: 75%;
  }
  .slider__btn--left {
    left: 72%;
  }

  .dots {
    display: none;
  }
}
@media (max-width: 575px) {
  .section--sign-up .btn {
    font-size: 1.4rem;
    padding: 2rem 3rem;
  }

  .slider__btn {
    top: 90%;
  }

  .testimonial::before {
    display: none;
  }
  .testimonial__author {
    margin-left: 0;
  }
  .testimonial__text {
    margin-bottom: 1rem;
  }
}
/* NAVIGATION */
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 9rem;
  width: 100%;
  z-index: 200;
}
.nav__logo {
  height: 4.5rem;
  transition: all 0.3s;
}
.nav__links {
  display: flex;
  align-items: center;
  list-style: none;
}
.nav__item {
  margin-left: 4rem;
}
.nav__link:visited, .nav__link:link {
  font-size: 1.7rem;
  font-weight: 400;
  color: inherit;
  text-decoration: none;
  display: block;
  transition: all 0.3s;
}
.nav__link--btn:link, .nav__link--btn:visited {
  padding: 0.8rem 2.5rem;
  border-radius: 3rem;
  background-color: var(--color-primary);
  color: #222;
}
.nav__link--btn:hover, .nav__link--btn:active {
  color: inherit;
  background-color: var(--color-primary-darker);
  color: #333;
}
.nav__button {
  cursor: pointer;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: none;
}
.nav__hamburger {
  position: relative;
}
.nav__hamburger span {
  width: 4rem;
  height: 1px;
  background-color: black;
  display: block;
}
.nav__hamburger:before {
  content: "";
  position: absolute;
  display: block;
  background-color: black;
  width: 120%;
  height: 1px;
  top: 0;
  left: 0;
  transform: translateY(-8px);
  transition: 0.3s;
}
.nav__hamburger::after {
  content: "";
  position: absolute;
  display: block;
  background-color: black;
  width: 80%;
  height: 1px;
  top: 0;
  left: 0;
  transform: translateY(8px);
  transition: 0.3s;
}
.nav .clicked .nav__hamburger span {
  opacity: 0;
}
.nav .clicked .nav__hamburger:before {
  width: 100%;
  transform: rotate(45deg);
}
.nav .clicked .nav__hamburger::after {
  width: 100%;
  transform: rotate(-45deg);
}

/* nav mobile */
.nav-mobile {
  background-color: white;
  height: 100vh;
  width: 100vw;
  position: fixed;
  list-style: none;
  display: none;
  animation: slide-right 0.4s;
  z-index: 100;
}
.nav-mobile__links {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.nav-mobile__link {
  margin-bottom: 2rem;
}
.nav-mobile__link a {
  text-decoration: none;
  font-size: 4rem;
  color: #444;
}
.nav-mobile__link:hover {
  text-decoration: underline;
}

.active {
  display: block;
}

.hide {
  display: none;
}

/* nav and stickly class at the same time */
.nav.sticky {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.95);
  animation: scroll-down 0.4s ease;
}

@keyframes scroll-down {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slide-right {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@media (max-width: 991px) {
  .nav__links {
    display: none;
  }
  .nav__button {
    display: flex;
  }
}
@media (max-width: 767px) {
  .nav-mobile__link a {
    font-size: 3rem;
  }
}
.header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__title {
  flex: 1;
  display: grid;
  grid-template-columns: 3fr 2fr;
  row-gap: 3rem;
  align-content: center;
  justify-content: center;
  align-items: start;
  justify-items: start;
}
.header__img {
  width: 100%;
  grid-column: 2/3;
  grid-row: 1/span 4;
  transform: translateY(-6rem);
}
.header .highlight {
  position: relative;
}
.header .highlight::after {
  display: block;
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  opacity: 0.7;
  transform: scale(1.07, 1.05) skewX(-15deg);
  background-image: var(--gradient-primary);
}

@media (max-width: 991px) {
  .header__title {
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .header__img {
    position: absolute;
    z-index: -200;
    width: 80%;
    height: 50vh;
    top: 10rem;
    opacity: 0.5;
    pointer-events: none;
  }
}
@media (max-width: 767px) {
  .header {
    position: relative;
  }
}
@media (max-width: 575px) {
  .header__title {
    left: 10%;
  }
  .header__img {
    display: none;
  }
}
.footer {
  padding: 10rem 0;
  background-color: #37383d;
}
.footer__nav {
  list-style: none;
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
}
.footer__item {
  margin-right: 4rem;
}
.footer__link {
  font-size: 1.6rem;
  color: #eee;
  text-decoration: none;
}
.footer__logo {
  height: 5rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 5rem;
}
.footer__copyright {
  font-size: 1.4rem;
  color: #aaa;
  text-align: center;
}
.footer__copyright .footer__link {
  font-size: 1.4rem;
}

@media (max-width: 767px) {
  .footer__nav {
    flex-wrap: wrap;
  }
  .footer__item {
    margin: 0 2rem;
    margin-bottom: 1rem;
  }
}
/* MODAL WINDOW */
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 60rem;
  background-color: #f3f3f3;
  padding: 5rem 6rem;
  box-shadow: 0 4rem 6rem rgba(0, 0, 0, 0.3);
  z-index: 1000;
  transition: all 0.5s;
}
.modal__header {
  font-size: 3.25rem;
  margin-bottom: 4.5rem;
  line-height: 1.5;
}
.modal__form button {
  grid-column: 1/span 2;
  justify-self: center;
  margin-top: 1rem;
}
.modal__control {
  margin-bottom: 2rem;
}
.modal__control small {
  display: block;
  margin-top: 1rem;
  font-size: 1.5rem;
}
.modal__control.error {
  color: var(--color-tertiary-darker);
}
.modal__input {
  font-size: 1.7rem;
  padding: 1rem 1.5rem;
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  width: 100%;
}
.modal__input:focus {
  outline: 1px solid var(--color-primary);
}
.modal__label {
  font-size: 1.7rem;
  font-weight: 500;
}

.btn--close-modal {
  font-family: inherit;
  color: inherit;
  position: absolute;
  top: 0.5rem;
  right: 2rem;
  font-size: 4rem;
  cursor: pointer;
  border: none;
  background: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  z-index: 100;
  transition: all 0.5s;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

@media (max-width: 767px) {
  .modal {
    width: 90%;
    padding: 3rem 4rem;
  }
}
@media (max-width: 575px) {
  .modal {
    padding: 2rem 2rem;
  }
}